@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  border-style: none;
  object-fit: contain;
}

body {
  margin: 0 auto;
  font-family: "Arial", sans-serif;
  color: #444444;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.site-list {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  font-family: "Oswald", "Arial Narrow", sans-serif;
  text-align: center;
  list-style: none;
}
@media (min-width: 768px) {
  .site-list {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .site-list {
    font-size: 20px;
    line-height: 30px;
  }
}

.site-list__item {
  padding-top: 22px;
  padding-bottom: 23px;
}
@media (max-width: 767px) {
  .site-list__item {
    border-top: 1px solid #e6e6e6;
  }
  .site-list__item:last-child {
    border-bottom: 1px solid #e6e6e6;
  }
}
@media (min-width: 768px) {
  .site-list__item {
    margin-right: 0;
    margin-left: 36px;
    padding-top: 17px;
  }
}
@media (min-width: 1440px) {
  .site-list__item {
    margin-left: 35px;
  }
}

.site-list__link {
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
  padding-bottom: 6px;
}
@media (min-width: 768px) {
  .site-list__link {
    padding-top: 17px;
    padding-bottom: 11px;
  }
}
@media (min-width: 1440px) {
  .site-list__link {
    color: #111111;
  }
  .site-list__link--index {
    color: #ffffff;
  }
}
.site-list__link--active::after {
  content: "";
  position: absolute;
  height: 2px;
  left: 0;
  right: 0;
  bottom: 4px;
  width: 100%;
  background: #68b738;
}
@media (min-width: 1440px) {
  .site-list__link--active::after {
    background: #ffffff;
  }
}

.button {
  display: block;
  padding: 10px;
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  border: none;
}
.button--gray {
  background-color: #f2f2f2;
  color: #444444;
}

.promo {
  position: relative;
  margin-bottom: 20px;
  font-weight: 400;
  font-family: "Oswald", "Arial Narrow", sans-serif;
  text-align: center;
  color: #ffffff;
}
.promo::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 302px;
  background-image: url("../img/index-background-mobile.jpg"), linear-gradient(to right, #65a93c 50%, #63a73a 50%);
  background-position: top center;
  background-repeat: no-repeat;
}
@media (min-resolution: 2dppx) {
  .promo::before {
    background-image: url("../img/index-background-mobile@2x.jpg"), linear-gradient(to right, #65a93c 50%, #63a73a 50%);
    background-size: 320px auto, 100% 100%;
  }
}
@media (min-width: 768px) {
  .promo {
    margin-bottom: 27px;
    min-height: 830px;
    color: #000000;
    background-image: none;
  }
  .promo::before {
    display: none;
  }
  .promo::after {
    content: "";
    position: absolute;
    top: 252px;
    left: 0;
    right: 0;
    z-index: -1;
    display: block;
    height: 709px;
    background-image: url("../img/index-can-tablet.jpg");
    background-position: top center;
    background-repeat: no-repeat;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .promo::after {
    background-image: url("../img/index-can-tablet@2x.jpg");
    background-size: 709px auto;
  }
}
@media (min-width: 1440px) {
  .promo {
    padding-top: 47px;
    min-height: 500px;
    color: #000000;
  }
  .promo::after {
    display: none;
  }
}

@media (min-width: 768px) {
  .promo__image {
    display: none;
  }
}

.promo__title {
  margin-top: 0;
  margin-bottom: 24px;
  padding-top: 28px;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}
@media (min-width: 768px) {
