    position: relative;
    z-index: 2;
    max-width: 47%;
    margin-bottom: 38px;
    margin-left: 80px;
  }
  .promo__title::after {
    content: "";
    position: absolute;
    top: -33px;
    left: 371px;
    right: 0;
    z-index: 1;
    display: block;
    height: 498px;
    width: 552px;
    background-image: url("../img/index-can-desktop.png");
    background-position: top center;
    background-repeat: no-repeat;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
