.description__item {
  position: relative;
  padding-left: 80px;
  min-height: 70px;
}
@media (max-width: 320px) {
  .description__item:not(:last-child) {
    margin-bottom: 10px;
  }
}
.description__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
@media (min-width: 768px) {
