    line-height: 24px;
    margin: 25px 0 0;
    padding: 100px 62px 0 0;
    width: 46%;
    min-height: 290px;
  }
  .description__item::after {
    position: absolute;
    top: -40px;
    left: 117px;
    z-index: -1;
    counter-increment: myCounter;
    content: counter(myCounter);
    color: #f2f2f2;
    display: inline-block;
    font-family: "Oswald", "Arial Narrow", sans-serif;
    font-size: 281px;
    line-height: 281px;
    text-align: right;
